import {Component, Input, OnInit} from '@angular/core';
import {IProject, Article} from '../../core/models';

@Component({
  selector: 'app-project-card',
  templateUrl: './project-card.component.html',
  styleUrls: ['./project-card.component.scss']
})
export class ProjectCardComponent implements OnInit {

  // @Input() projectData: IProject;
  @Input() projectData: Article;
  @Input() isUser = false;

  constructor() {
  }

  ngOnInit(): void {
  }

  public getInitials(str): string {
    str = str.trim();

    if (!str.length) {
      return '..';
    }

    const strArray = str.split(' ');
    if (strArray.length === 1) {
      return strArray[0][0].toUpperCase();
    }

    return `${strArray[0][0].toUpperCase()}${strArray[strArray.length - 1][0].toUpperCase()}`;
  }

  public projectCreationDate(createdAt: string) {
    if (createdAt !== '' && createdAt) {
      return createdAt.split('T')[0];
    } else {
      return '';
    }
  }
}
